function phoneConsole(text) {
    var $div = '';

    if ($('body').find('.phoneConsole').length) {
        $div = $('body .phoneConsole');

    } else {
        $div = $('<div>', { 'class': 'phoneConsole' });
        $div.css({
            position: 'fixed',
            zIndex: '9999',
            top: 0,
            left: 0,
            bottom: '50%',
            width: '100%',
            background: 'rgba(255,0,0,0.5)',
            color: '#fff',
            fontSize: '12px',
            lineHeight: '1.2em',
            overflow: 'scroll'
        })
        $div.bind('touchstart', function(e) {
            e.stopPropagation();
        })
        $('body').append($div);
    }
    var _html = $div.html();
    _html += '<p>' + text + '</p>';
    $div.html(_html);
}

var brandPage = {
    dom: function() {
        var _this = this;
        // _this.basePath = basePath;
        _this.brandAside = $('.brand-aside');
        _this.brandContent = $('.brand-content');
        _this.brandFilter = $('.brand-filter');
        _this.brandAllList = $('.brand-all-list');
        _this.brandFilterTab = _this.brandFilter.find('.tab');
        _this.brandFilterWrap = _this.brandFilter.find('.list');
    },
    request: function(url, param, callback, fail, type, dataType) {
        var _this = this;
        var _url = url,
            _param = param || {},
            _type = type || 'GET',
            _dataType = dataType || 'JSON';
        $.ajax({
            url: _url,
            data: param,
            type: _type,
            dataType: _dataType
        }).done(function(data) {
            callback && callback(data);
        }).fail(function(err) {
            console.log(err);
            fail && fail(err);
        });
    },
    imgLoaded: function(imgs, callback) {
        var _this = this;
        if (!imgs.length) {
            callback && callback();
            return;
        };
        var _length = imgs.length;
        var _count = 0;

        $.each(imgs, function(i, n) {
            if (n.complete) {
                console.log(_count == _length);
                _count++;
                _count == _length && callback && callback();
            } else {
                $(n).load(function() {
                    // phoneConsole(_count)
                    _count++;
                    _count == _length && callback && callback();
                });
            }
        });
    },
    appEnter: function(param) {
        var _this = this;
        if (param == 'brand') {
            _this.hotBrandInit();
        } else if (param == 'filter') {
            _this.brandAside.find('.item').first().trigger('click');
            _this.filterInit('tab');
        } else if (param == 'ios') {
            if (_iosFromFilter && _iosFromFilter != null && _iosFromFilter != '') {
                wSStorage.set("filter", _iosFromFilter);
            } else if (_iosFromBrand && _iosFromBrand != null && _iosFromBrand != '') {
                wSStorage.set("brand", _iosFromBrand);
            }
        }
    },
    loadAllBrand: function() {
        var _this = this;
        var _basePath = _this.basePath;
        var _url = _basePath + '/brand/infoList';
        var _brandData = {};

        _this.request(_url, {}, function(data) {
            if (data.code == 100) {
                var _message = data.message;
                var _html = '<div class="swiper-container"><div class="swiper-wrapper"><div class="swiper-slide"><span class="item active" data-id="filter"><i class="f-icon"></i>筛选</span><span class="item" data-id="all">全部</span>';
//                 console.log(_message);
                $.each(_message, function(i, n) {
                    var _brandId = n['b_id'];
                    var _brandName = n['b_name'];

                    _html += '<span class="item" data-id="' + _brandId + '">' + _brandName + '</span>';
                    _brandData[_brandId] = n['takeSeriesInfo'];
                    _brandData['brandBanner' + _brandId] = n['b_bgimg'];
                    _brandData['brandBannerUrl' + _brandId] = n['b_url'];
                });
                _html += '</div></div></div>';
                _this.brandData = _brandData;
                _this.brandAside.html(_html);

                _this.asideSwiper = new Swiper('.brand-aside .swiper-container', {
                    //scrollbar: '.swiper-scrollbar',
                    direction: 'vertical',
                    slidesPerView: 'auto',
                    mousewheelControl: true,
                    freeMode: true
                });
            }
            // console.log(_message);
            // console.log(_this);

            //针对首页热门品牌跳入分类页，初始分类页样式
            _this.hotBrandInit();
        });

    },
    loadAllSeries: function() {
        var _this = this;
        var _basePath = _this.basePath;
        var _url = _basePath + '/brand/seriesInfoList';
        var _brandData = {};

        _this.request(_url, {}, function(data) {
            if (data.code == 100) {
                var _message = data.message;
                //数据第一个为其它类型，放在最后显示
                if (_message[0]['firstLetter'] == '0') {
                    var _lastList = _message.shift();
                    _message.push(_lastList);
                }

                var _html = '<div class="swiper-container"><div class="swiper-wrapper"><div class="swiper-slide">';

                $.each(_message, function(i, n) {
                    var _letter = n['firstLetter'];
                    var _list = n['seriesInfoList'];

                    _letter == 0 && (_letter = '其它');
                    _html += '<dl><dt>' + _letter + '</dt>';



                    $.each(_list, function(x, y) {
                        var _bId = y['b_id'];
                        var _sName = y['name'];
                        var _sId = y['s_id'];
                        var _sDesc = y['desc'];
                        var _sSrc = y['image'] + '?imageMogr2/auto-orient/thumbnail/200x/strip/quality/80';
                        var _minPrice = y['minPrice'];
                        var _sNum = y['productCount'];
                        var _sType = y['seatDes'].join('</span>|<span>');
                        // var _sType = y['seatDes'].toString();
                        var _sHref = '/series/' + _bId + '/' + _sId;

                        _html += '<dd><a href="' + _sHref + '" class="clearFix">\
                                    <div class="lf">\
                                        <img src="' + _sSrc + '" >\
                                        <span class="price">￥' + _minPrice + '万起</span>\
                                    </div>\
                                    <div class="rt">\
                                        <h3><span class="">' + _sName + '</span><span class="num">' + _sNum + '</span></h3>\
                                        <p class="type"><span>' + _sType + '</span></p>\
                                        <p class="des">' + _sDesc + '</p>\
                                    </div>\
                                </a></dd>';
                    });
                    _html += '</dl>'
                });
                _html += '</div></div></div>';
                // console.log(_message);
                _this.brandAllList.html(_html).addClass('dom-loaded');
                _this.allListSwiper = new Swiper('.brand-all-list .swiper-container', {
                    //scrollbar: '.swiper-scrollbar',
                    direction: 'vertical',
                    slidesPerView: 'auto',
                    mousewheelControl: true,
                    freeMode: true
                });
            }
        })
    },
    loadBrandSeries: function(id) {
        if (!id) return;
        var _this = this;
        var _listCls = 'brand-list-' + id;
        var $brandList = $('.' + _listCls);
        var _seriesSwiper = {};
        var _brandData = _this.brandData;
//        console.log(_brandData);
        var _bannerHref = _brandData['brandBannerUrl' + id] ? _brandData['brandBannerUrl' + id] : 'javascript:void(0);'
        if (!$brandList.length) {
            var _bannerSrc = _brandData['brandBanner' + id] + '?imageMogr2/auto-orient/thumbnail/600x/strip/quality/80';
            var _html = '<div class=" brand-list-base ' + _listCls + '"><div class="swiper-container"><div class="swiper-wrapper"><div class="swiper-slide">';
            _html += '<a href="' + _bannerHref + '"><img class="banner" src="' + _bannerSrc + '"></a>';


            $.each(_brandData[id], function(x, y) {
                var _letter = y['firstLetter'];
                var _sList = y['seriesInfoList'];

                _html += '<dl><dt>' + _letter + '</dt>';
                $.each(_sList, function(i, n) {
                    var _bId = n['b_id'];
                    var _sName = n['name'];
                    var _sId = n['s_id'];
                    var _sDesc = n['desc'];
                    var _sSrc = n['image'] + '?imageMogr2/auto-orient/thumbnail/200x/strip/quality/80';
                    var _minPrice = n['minPrice'];
                    var _sNum = n['productCount'];
                    var _sType = n['seatDes'].join('</span>|<span>');
                    // var _sType = y['seatDes'].toString();
                    var _sHref = '/series/' + _bId + '/' + _sId;

                    _html += '<dd><a href="' + _sHref + '" class="clearFix">\
                                <div class="lf">\
                                    <img src="' + _sSrc + '" >\
                                    <span class="price">￥' + _minPrice + '万起</span>\
                                </div>\
                                <div class="rt">\
                                    <h3><span class="">' + _sName + '</span><span class="num">' + _sNum + '</span></h3>\
                                    <p class="type"><span>' + _sType + '</span></p>\
                                    <p class="des">' + _sDesc + '</p>\
                                </div>\
                            </a></dd>';
                });
                _html += '</dl>';
            });
            // _html += _listCls;
            _html += '</div></div></div></div>';
            var $html = $(_html);
            _this.brandContent.append($html);

            _this['seriesSwiper' + id] = new Swiper('.' + _listCls + ' .swiper-container', {
                //scrollbar: '.swiper-scrollbar',
                direction: 'vertical',
                slidesPerView: 'auto',
                mousewheelControl: true,
                freeMode: true
            });

            // console.log(_this);
        } else {
            _this['seriesSwiper' + id] && _this['seriesSwiper' + id].slideTo(0, 0, false); //切换到第一个slide，速度为0秒
            $brandList.show();
        }

    },
    bind: function() {
        var _this = this;
        var _basePath = _this.basePath;

        //筛选中 价格 车身 坐位 事件。
        _this.brandFilterTab.find('li').bind('click', function() {
            var $this = $(this);
            var _index = $this.index();
            var _type = $this.attr('data-type');
            var _listCls = 'filter-list-' + _type;
            var $list = _this.brandFilterWrap.find('.' + _listCls);


            $this.addClass('active').siblings().removeClass('active');
            _this.resetSwiper();
            if (!$list.hasClass('dom-loaded')) {
                var _url = _basePath + '/brand/seriesInfo/' + _type;
                _this.request(_url, {}, function(data) {
                    if (data.code == 100) {
                        var _message = data.message;
                        var _html = '<div class="swiper-wrapper"><div class="swiper-slide">';
//                        console.log(_message);
                        $.each(_message, function(i, n) {
                            var _title = n['firstLetter'];
                            var _list = n['seriesInfoList'];

                            _html += '<dl><dt id="' + escape(_title) + '">' + _title + '</dt><dd class="clearFix">';

                            $.each(_list, function(x, y) {
                                var _name = y['name'];
                                var _minPrice = y['minPrice'];
                                var _src = y['image'] + '?imageMogr2/auto-orient/thumbnail/200x/strip/quality/80';
                                var _href = _basePath + '/series/' + y['b_id'] + '/' + y['s_id'];

                                _html += '<a href="' + _href + '"><img src="' + _src + '" ><h3>' + _name + '</h3><p class="price">￥' + _minPrice + '万起</p></a>';

                            });
                            _html += '</dd>';
                        });
                        _html += '</div></div>';
                        var $html = $(_html);
                        $list.append($html).addClass('dom-loaded');
                        _this['filterSwiper' + _type] = new Swiper('.brand-filter .' + _listCls, {
                            //scrollbar: '.swiper-scrollbar',
                            direction: 'vertical',
                            slidesPerView: 'auto',
                            mousewheelControl: true,
                            freeMode: true
                        });
                        _this.filterInit('swiper');
                    }
                    // console.log(data);
                });
            } else {
               
                console.log('已存在');
            }

            _this.brandFilterWrap.css({
                    webkitTransition: '-webkit-transform .5s',
                    transition: 'transform .5s',
                    webkitTransform: 'translate(' + _this.brandFilterWrap.width() * _index * -1 + 'px, 0)',
                    transform: 'translate(' + _this.brandFilterWrap.width() * _index * -1 + 'px, 0)'
                })
                // _this.brandFilterWrap[0].style.transition = '-webkit-transform .5s';
                // _this.brandFilterWrap[0].style.transform = 'translate(' + _this.brandFilterWrap.width() * _index * -1 + 'px, 0)';
                // _this.brandFilterWrap[0].style.webkitTransform = 'translate(' + _this.brandFilterWrap.width() * _index * -1 + 'px, 0)';
        });


        //左侧品牌列表 事件
        _this.brandAside.bind('click', function(e) {
            if (!$(e.target).hasClass('item')) return;

            var $this = $(e.target);
            var _id = $this.attr('data-id');
            $this.addClass('active').siblings().removeClass('active');

            _this.brandContent.children().hide();
            _this.resetSwiper();
            if (_id == 'filter') {
                // console.log(_this);
                _this.brandFilter.show();
            } else if (_id == 'all') {
                _this.brandAllList.show();
                !_this.brandAllList.hasClass('dom-loaded') && _this.loadAllSeries();
            } else {
                _this.loadBrandSeries(_id);
            }
            // console.log(_this.asideSwiper.getWrapperTranslate());
            // console.log(_id);
        });
    },
    //首页精准找车点击进入分类页时初始化
    filterInit: function(param) {
        var _this = this;
        var _filterValue = ''
        if (_filterValue = wSStorage.get('filter')) {
            console.log(_filterValue);
            console.log(unescape(_filterValue));
            _filterValue = $.parseJSON(_filterValue);

            if (param == 'tab') {
                var _str = 'li[data-type=' + unescape(_filterValue.type) + ']';
                _this.brandFilterTab.find(_str).trigger('click');
            } else if (param == 'swiper') {
                var _listCls = 'filter-list-' + _filterValue.type;
                var $list = _this.brandFilterWrap.find('.' + _listCls);

                // $list.css('visibility', 'hidden');
                _this.imgLoaded($list.find('img'), function() {
                    // console.log(_filterValue);
                    var _str = '[id="' + _filterValue.btnValue + '"]';
                    var _posY = $list.find(_str).parent().position().top * -1;
                    _this['filterSwiper' + _filterValue.type].setWrapperTranslate(_posY);
                    // $list.css('visibility', 'visible');
                    wSStorage.del('filter')
                    console.log(localStorage);
                });
            }

        } else {
            if (param == 'tab') {
                var _str = 'li:first';
                _this.brandFilterTab.find(_str).trigger('click');
            } else if (param == 'swiper') {

            }
        }
    },
    //首页热门品牌点击进入分类页时初始化
    hotBrandInit: function() {
        var _this = this;
        var _brand = ''
        if (_brand = wSStorage.get('brand')) { //全部
            var _str = '.item[data-id="' + _brand + '"]'
            _this.brandAside.find(_str).trigger('click');
            wSStorage.del('brand');
            // console.log(localStorage);
            // console.log(_brand);
        }
    },
    //初始化 滚动位置
    resetSwiper: function() {
        var _this = this;
        _this.allListSwiper && _this.allListSwiper.slideTo(0, 0, false); //切换到第一个slide，速度为0秒
        // _this.asideSwiper && _this.asideSwiper.slideTo(0, 0, false); //切换到第一个slide，速度为0秒
        _this.filterSwiper1 && _this.filterSwiper1.slideTo(0, 0, false); //切换到第一个slide，速度为0秒
        _this.filterSwiper2 && _this.filterSwiper2.slideTo(0, 0, false); //切换到第一个slide，速度为0秒
        _this.filterSwiper3 && _this.filterSwiper3.slideTo(0, 0, false); //切换到第一个slide，速度为0秒
    },
    //页面进入时，根据上级页面点击入口，定位滚动位置
    initSwiper: function(swiper, posY) {
        var _this = this;
        if (!swiper) return;
    },
    init: function() {
        var _this = this;
        //ios首页进入wap分类页时需要获取localStorage
       

        _this.dom();
        _this.bind();


        //初始化筛选页签占位列表div
        _this.brandFilterTab.find('li').each(function() {
            var $this = $(this);
            var _type = $this.attr('data-type');
            var _listCls = 'filter-list-' + _type;
            var $scrollWrap = $('<div>', { 'class': 'filter-list swiper-container ' + _listCls });
            _this.brandFilterWrap.append($scrollWrap);
            // $this.index() == 0 && $this.trigger('click');
        });
        _this.filterInit('tab');
        _this.loadAllBrand();


        // _this.loadAllSeries();

    }
}
var wSStorage = {
    set: function(key, value, callback) {
        if (!window.localStorage) {
            // alert('浏览器不支持localStorage');
            return;
        }
        if (!key) return;
        localStorage.setItem(key, value);
        callback && callback();
    },
    get: function(key, callback) {
        if (!window.localStorage) {
            // alert('浏览器不支持localStorage');
            return;
        }
        if (!key) return;
        if (localStorage.getItem(key)) {
            return localStorage.getItem(key);
        } else {
//            console.log('localStorage 中不存在: ' + key);
        }
    },
    del: function(key, callback) {
        if (!window.localStorage) {
            // alert('浏览器不支持localStorage');
            return;
        }
        if (!key) return;
        localStorage.removeItem(key);
    }
}
brandPage.init();
