<template>
  <!-- 注册组件 -->
  <div id="login">
    <div class="doc login_doc">
      <div class="h_login">
        <div class="bg_div">
          <div class="block clearFix">
            <div class="form">
              <div class="head">
                <div class="hd_Image">
                  <img src="../assets/img/hd_bg01.png" class="h_img" />
                </div>
              </div>
              <ul>
                <li>
                  <span class="bg">
                    <label for class="phoneIc"></label>
                    <input
                      type="text"
                      placeholder="请输入手机号码"
                      name="username"
                      id="J_Username"
                      style="color: rgb(153, 153, 153);"
                      maxlength="11"
                      v-model="userphone"
                    />
                  </span>
                </li>
                <li>
                  <span class="bg">
                    <label for class="phoneIc"></label>
                    <input
                      type="text"
                      placeholder="请输入密码"
                      name="username"
                      id="J_Username"
                      style="color: rgb(153, 153, 153);"
                      maxlength="11"
                      v-model="psw"
                    />
                  </span>
                </li>
                <li class="yzmHidden">
                  <div class="yzmSlide">
                    <div class="box mb10">
                      <span class="bg bg2">
                        <label for class="yzmIc"></label>
                        <input
                          type="text"
                          placeholder="请输入验证码"
                          class="yzm"
                          name="password"
                          id="J_Password"
                          style="color: rgb(153, 153, 153);"
                          maxlength="6"
                          v-model="checkcode"
                        />
                      </span>
                      <span id="timer" class="yzmBtn">发送验证码</span>
                    </div>
                  </div>
                </li>
                <li class="liCheck">
                  <input type="checkbox" name id class="checkBox" @click="btnActive=!btnActive" />我已经阅读并同意
                  <a href="http://m.htche.com:80/agreement">《海淘车用户注册协议》</a>
                </li>
                <li>
                  <input id="registerBtn" type="submit" class="submit" :class="btnActive?'':'disable'"  value="马上注册" @click="btnActive?Register():''" />
                </li>
              </ul>
              <div class="errorBox">
                <span class="tip"></span>
              </div>
              <div class="h_foot">
                <a href="/" id="login_out">
                  <img
                    class="out_img"
                    src="../assets/img/lg_out.png"
                    style="width:10%"
                  />
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
    name: 'login',
    data(){
        return{
            userphone: '',
            psw: '',
            checkcode: '',
            btnActive: false,
        }
    },
    methods:{
        Register(){
          if(this.userphone == '' || this.psw == ''){
            alert("请正确输入用户电话和密码！！！");
          }else{
            let userdata = `userphone=${this.userphone}&psw=${this.psw}`;
            this.$axios.get('http://localhost:3000/users/reg?'+userdata).then((result)=>{
              console.log(result);
              if(result.data.code == 1){
                let status = confirm("您已经成功注册，是否前往登录页面？");
                if(status){
                  this.$router.push({
                    path: '/login',
                  });
                }
              }else{
                alert(result.data.msg);
              }
            });
          }
        }
    },
};
</script>
<style>
    @import "../assets/css/login.css";
</style>