<template>
  <div>
    <div class="brand-header">
      <div class="wap_nav clearFix header_wap">
        <a href="javascript:history.back()" class="rtn demo-icon" id="seriesBackA"></a>
        <h1 class="wap_logo">
          <a href="/">
            <span class="img css_spd"></span>
          </a>
        </h1>
        <div class="search">
          <span class="clear" style="display: none;">×</span>
          <span class="demo-icon"></span>
          <form action="javascript:return true;">
            <input
              type="search"
              name
              placeholder="搜索车型"
              id="search"
              data-setting="{&quot;url&quot;:&quot;http://m.htche.com:80/search/view&quot;}"
              value
            />
          </form>
        </div>
        <div class="login">
          <a
            id="loginId"
            class="icon-wap_5"
            href="http://m.htche.com:80/login?upUrl=http://m.htche.com/brand/view"
          ></a>
        </div>
      </div>
    </div>

    <div class="brand-filter" style>
      <ul class="tab clearFix">
        <li data-type="1" @click="clickfn(1)" :class="n==1?'active':''">
          <span>价格</span>
        </li>
        <li data-type="2" @click="clickfn(2)" :class="n==2?'active':''">
          <span>车身</span>
        </li>
        <li data-type="3" @click="clickfn(3)" :class="n==3?'active':''">
          <span>座数</span>
        </li>
      </ul>
      <div class="list" style="transition: transform 0.5s ease 0s; transform: translate(0px, 0px);">
        <div
          class="filter-list swiper-container filter-list-1 dom-loaded swiper-container-vertical swiper-container-free-mode"
        >
          <div class="swiper-wrapper">
            <div class="swiper-slide swiper-slide-active">
              <dl v-for="(item,index) in screenData" :key="index">
                <dt id="20-30%u4E07">{{item.firstLetter}}</dt>
                <dd class="clearFix">
                  <router-link
                    :to="'/detail/'+items.b_id+'/'+items.s_id"
                    v-for="(items,index) in item.seriesInfoList"
                    :key="index"
                  >
                    <img :src="items.image" />
                    <h3>{{items.name}}</h3>
                    <p class="price">￥{{items.minPrice}}万起</p>
                  </router-link>
                </dd>
              </dl>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      screenData: [],
      n: 1
    };
  },
  methods: {
    clickfn(n) {
      axios.get(`http://m.htche.com/brand/seriesInfo/${n}`).then(res => {
        this.screenData = res.data.message;
      });
      this.n = n;
    }
  },
  created() {
    axios.get("http://m.htche.com/brand/seriesInfo/1").then(res => {
      this.screenData = res.data.message;
      console.log(this.screenData);
    });
  }
};
</script>

<style scoped>
@import "../assets/css/base.css";
@import "../assets/css/brand.css";
</style>