<template>
  <!-- 登录组件 -->
  <div id="login">
    <div class="doc login_doc">
      <div class="h_login">
        <div class="bg_div">
          <div class="block clearFix">
            <div class="form">
              <div class="head">
                <div class="hd_Image">
                  <img src="../assets/img/hd_bg01.png" class="h_img" />
                </div>
              </div>
              <ul>
                <li>
                  <span class="bg">
                    <label for class="phoneIc"></label>
                    <input
                      type="text"
                      placeholder="请输入手机号码"
                      name="username"
                      id="J_Username"
                      style="color: rgb(153, 153, 153);"
                      maxlength="11"
                      v-model="userphone"
                    />
                  </span>
                </li>
                <li>
                  <span class="bg">
                    <label for class="phoneIc"></label>
                    <input
                      type="text"
                      placeholder="请输入密码"
                      name="username"
                      id="J_Username"
                      style="color: rgb(153, 153, 153);"
                      maxlength="11"
                      v-model="psw"
                    />
                  </span>
                </li>
                <li class="yzmHidden">
                  <div class="yzmSlide">
                    <div class="box mb10">
                      <span class="bg bg2">
                        <label for class="yzmIc"></label>
                        <input
                          type="text"
                          placeholder="请输入验证码"
                          class="yzm"
                          name="password"
                          id="J_Password"
                          style="color: rgb(153, 153, 153);"
                          maxlength="6"
                          v-model="checkcode"
                        />
                      </span>
                      <span id="timer" class="yzmBtn">发送验证码</span>
                    </div>
                  </div>
                </li>
                <li class="liCheck">
                  <input type="checkbox" name id class="checkBox" @click="btnActive=!btnActive" />我已经阅读并同意
                  <a href="http://m.htche.com:80/agreement">《海淘车用户注册协议》</a>
                </li>
                <li>
                  <input
                    id="loginBtn"
                    type="submit"
                    class="submit"
                    :class="btnActive?'':'disable'"
                    value="马上登录"
                    @click="btnActive?Login():''"
                  />
                </li>
                <li>
                  <a href="/reg">没有账号，去注册</a>
                </li>
              </ul>
              <div class="errorBox">
                <span class="tip"></span>
              </div>
              <div class="h_foot">
                <a href="/" id="login_out">
                  <img class="out_img" src="../assets/img/lg_out.png" style="width:10%" />
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "login",
  data() {
    return {
      userphone: "",
      psw: "",
      checkcode: "",
      btnActive: false
    };
  },
  methods: {
    Login() {
      if (this.userphone == "" || this.psw == "") {
        alert("请正确输入用户电话及密码！！！");
      } else {
        let userdata = `userphone=${this.userphone}&psw=${this.psw}`;
        this.$axios
          .get("http://localhost:3000/users/login?" + userdata)
          .then(result => {
            console.log(result);
            if (result.data.code == 1) {
              window.localStorage.setItem("token", result.data.data);
              this.$router.push({
                path: "/"
              });
            } else {
              alert(result.data.msg);
            }
          });
      }
    },
    // 测试数据
    // getdata(){
    //   let bid = 2;
    //   let sid = 16;
    //   this.$axios.get('http://m.htche.com/search/item/list?query=%5B%7B%22fieldType%22%3A%22BRANDID%22%2C%22value%22%3A%22'+bid+'%22%7D%2C%7B%22fieldType%22%3A%22SERIESID%22%2C%22value%22%3A%22'+sid+'%22%7D%5D&order=PRICE_ASC&page=1&rows=24').then((result)=>{
    //     console.log(result);
    //   })
    // }
  },
  created() {
    // this.getdata();
  },
};
</script>
<style>
@import "../assets/css/login.css";
</style>