<script>
import Showlist from "./Showlist";
import Detailpop from "./Detail-pop";
import Swiper from "swiper";
export default {
  data() {
    return {  
      Disshow: false,
      idx: null,
      datalist: [],
      peolist: [],
      carlist: [],
      pop:{},
      n:0,
      // 控制切换
      isshow:false,
      // 控制对比
      Sisshow: false,
      showma:true
    };
  },
  components: {
    Showlist,
    Detailpop
  },
  watch:{
    idx(){
      this.$axios({
      url: `http://m.htche.com/sku/detail/show/${this.idx}`,
      method: "get"
    }).then(res => {
      this.datalist =
        res.data.message.conditionList[0].subValueSet[0].subValueSet;
      this.peolist = res.data.message.listComment;
      this.pop=res.data.message.conditionList[0]
    });
    this.$axios({
      url:`http://m.htche.com/rec/relate?userId=undefined&recnum=10&itemId=${this.idx}`,
      method: "get"
    }).then(res => {
      console.log(res);
      this.carlist = res.data.message;
    });
    }
  },
  created() {
    this.idx = this.$route.params.num;
    this.$axios({
      url: `http://m.htche.com/sku/detail/show/${this.idx}`,
      method: "get"
    }).then(res => {
      this.datalist =
        res.data.message.conditionList[0].subValueSet[0].subValueSet;
      this.peolist = res.data.message.listComment;
      this.pop=res.data.message.conditionList[0]
    });
    this.$axios({
      url:`http://m.htche.com/rec/relate?userId=undefined&recnum=10&itemId=${this.idx}`,
      method: "get"
    }).then(res => {
      this.carlist = res.data.message;
    });
  },
  mounted(){
    setTimeout(function(){
      new Swiper(".swiper-container2", {
      slidesPerView: 2,
      spaceBetween: 30,
      freeMode: true,
      pagination: {
        el: ".swiper-pagination1",
        clickable: true,
        type: "fraction"
      }
    });
      var mySwiper = new Swiper(".swiper-container1", {
      loop: true, // 循环模式选项
      autoplay: {
        stopOnLastSlide: true
      },
      pagination: {
        el: ".swiper-pagination",
        type: "fraction"
      }
    });
    },500)
    
  },
  methods:{
    myfn(){
      this.$store.commit('addMut',this.datalist[this.n])
    },
    childfn(){
      this.Sisshow=false
    }
  },
  computed:{
    list(){
      return this.$store.state.datalist
    }
  }
};
</script>

