<template>
  <div class="d-compare-layer show " :class="datalist.length==0?'null':isclass">
	<div class="title">对比车型</div>
	<div class="tip">只可对比2个车型</div>
	<span class="btn-clear" @click="delall">清空</span>
	<span class="btn-editOver" @click="isclass='',isshow=true">完成</span>
	<span class="btn-edit" @click="isclass='editStatus',isshow=false">编辑</span>
	<div class="noData">
		<span class="icon"></span>
		<span>请先添加要对比的车辆</span>
	</div>
	<div class="scroll-layer">
		<ul>
			<li v-for="(item,index) in datalist" :key="index">
			    <a class="clearFix">
			    	<span class="btn-compare" v-if="isshow">比</span>
			    	<span class="jy" v-if="!isshow" @click="del(index)">删</span>
			    	<img :src="item.productList[0].imagMap.focusPic[0].mediaSource+item.productList[0].imagMap.focusPic[0].mediaId" width="35%" class="img" alt="">
			        <div class="text">
			            <h4>{{item.productList[0].productName}}</h4>
			            <p class="p1"><span class="money"><b>￥{{item.productList[0].price/10000}}</b>万</span><span class="bs">{{item.productList[0].area}}</span><span class="bs xc">{{item.productList[0].stockStatusName}}</span></p>
			        </div>
			    </a>
			</li>
		</ul>
	</div>
	<div class="btn">
		<span class="btn-close" @click="myfn"></span>
		<span class="btn-ok disabled"></span>
	</div>
</div>
</template>

<script>
export default {
	computed:{
		datalist(){
			return this.$store.state.datalist
		}
	},
	methods:{
		myfn(){
			this.$emit('trans-click')
		},
		del(i){
			this.$store.commit('delMut',i)
		},
		delall(){
			this.$store.commit('delallMut')
		}
	},
	data(){
		return{
			isclass:'',
			isshow:true
			}
	}
}
</script>

<style>
.jy{
	position: absolute;
    left: 10px;
    top: 10px;
    z-index: 2;
    background: #cccccc;
    color: #fff;
    font-size: 12px;
    line-height: 1em;
    width: 1em;
    height: 1em;
    padding: 5px;
    border-radius: 100px;
    -webkit-transition: transform .3s;
    transition: transform .3s;
}
</style>