<template>
  <div class="brand-aside">
    <div class="swiper-container swiper-container-vertical swiper-container-free-mode">
      <div
        class="swiper-wrapper"
        style="transition-duration: 0ms; transform: translate3d(0px, 0px, 0px);"
      >
        <div class="swiper-slide swiper-slide-active">
          <router-link to="/list">
            <span
              class="item"
              data-id="filter"
              :class="sidebarId==-1?'active':''"
              @click="sidebarId=-1"
            >
              <i class="f-icon"></i>筛选
            </span>
          </router-link>
          <router-link to="/list/all">
            <span
              class="item"
              data-id="all"
              :class="sidebarId==-2?'active':''"
              @click="sidebarId=-2"
            >全部</span>
          </router-link>
          <router-link to="/list/list" v-for="(item,index) in listData" :key="index">
            <span
              class="item"
              :data-id="item.b_id"
              :class="sidebarId==item.b_id?'active':''"
              @click="fn(item.b_id,index)"
            >{{item.b_name}}</span>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Swiper from "swiper";
import "../assets/js/jquery-1.11.3.min.js";
import "../assets/js/brand.js";

export default {
  data() {
    return {
      listData: [],
      sidebarId: -1,
      i: 0
    };
  },
  created() {
    axios.get("http://m.htche.com/brand/infoList").then(res => {
      this.listData = res.data.message;
    });
  },
  methods: {
    fn(n, i) {
      this.sidebarId = n;
      this.$store.commit("vuxiMut", i);
    }
  }
};
</script>

<style scoped>
@import '../assets/css/brand.css';
@import '../assets/css/new-detail.css';
@import '../assets/css/common1.css';
@import '../assets/css/base1.css';
</style>