<template>
  <div id="hotcollect">
    <div class="doc fixed-head">
      <div class="wap_returnHead">
        <a href="javascript:history.back();" class="returnBtn">
          <span class="demo-icon"></span>
        </a>
        <div class="title">{{title}}</div>
        <div class="ctrl">
          <a class="clear-btn" style="display: none;">清空</a>
        </div>
      </div>
      <div class="hot-list-more">
        <ul>
          <li class="top-three" v-for="(item,index) in hotcollectdata" :key="index">
            <a href="#" class="clearFix">
              <span class="num">
                <b>{{index+1}}</b>
              </span>
              <img
                :src="item.headImg"
                width="35%"
                class="img"
                alt
              />
              <span class="mark" v-if="repStatus">&nbsp;{{item.number}} 条评价</span>
              <span class="mark mark-price" v-if="discStatus">&nbsp;{{item.differPrice/10000}} 万</span>
              <div class="text">
                <h4>{{item.productName}}</h4>
                <p class="p1">
                  <span class="money">
                    <b>￥{{item.price/10000}}</b>万
                  </span>
                  <span class="bs">{{item.area}}</span>
                  <span class="bs xc">{{item.stockStatusName}}</span>
                </p>
              </div>
            </a>
          </li>
          
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
    name: "hotcollect",
    data(){
        return{
            hotcollectdata:[],
            title: '',
            repStatus: false,
            discStatus: false,
            hotnum: this.$route.params.num,
        }
    },
    methods:{
        getcolData(){
            this.$axios.get('http://m.htche.com/collect/hot?page=0&rows=10').then((result)=>{
                this.hotcollectdata = result.data.message;
                console.log(this.hotcollectdata);
            });
        },
        gethotmodel(){
            this.$axios.get('http://m.htche.com/comment/hot').then((result)=>{
                this.hotcollectdata = result.data.message;
                console.log(this.hotcollectdata);
            });
        },
        getdischighest(){
            this.$axios.get('http://m.htche.com/discount/hot').then((result)=>{
                this.hotcollectdata = result.data.message;
                console.log(this.hotcollectdata);
            });
        }
    },
    created(){
      if(this.hotnum == 1){
        this.getcolData();
        this.title = "热门收藏";
      }else if(this.hotnum == 2){
        this.gethotmodel();
        this.title = "热评车型";
        this.repStatus = true;
      }else if(this.hotnum == 3){
        this.getdischighest();
        this.title = "优惠最高";
        this.discStatus = true;
      }
        console.log(this.hotnum);
    },

};
</script>
<style>
    @import '../assets/css/index.css';
</style>