<template>
  <div id="hotmodel">
    <div class="doc fixed-head">
      <div class="wap_returnHead">
        <a href="javascript:history.back();" class="returnBtn">
          <span class="demo-icon"></span>
        </a>
        <div class="title">热评车型</div>
        <div class="ctrl">
          <a class="clear-btn" style="display: none;">清空</a>
        </div>
      </div>
      <div class="hot-list-more">
        <ul>
          <li class="top-three" v-for="(item,index) in hotmodeldata" :key="index">
            <a href="#" class="clearFix">
              <span class="num">
                <b>{{index+1}}</b>
              </span>
              <img
                :src="item.headImg"
                width="35%"
                class="img"
                alt
              />
              <span class="mark">&nbsp;{{item.number}} 条评价</span>
              <div class="text">
                <h4>{{item.productName}}</h4>
                <p class="p1">
                  <span class="money">
                    <b>￥{{item.price/10000}}</b>万
                  </span>
                  <span class="bs">{{item.area}}</span>
                  <span class="bs xc">{{item.stockStatusName}}</span>
                </p>
              </div>
            </a>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
    name: 'hotmodel',
    data(){
        return{
            hotmodeldata:[],
        }
    },
    methods:{
        gethotmodel(){
            this.$axios.get('http://m.htche.com/comment/hot').then((result)=>{
                this.hotmodeldata = result.data.message;
                console.log(this.hotmodeldata);
            });
        }
    },
    created(){
        this.gethotmodel();
    }
};
</script>
<style>
@import "../assets/css/index.css";
</style>