import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';
import Search from '@/components/Search';
import Diary from '@/components/Diary';
import DiaryTop from '@/components/DiaryTop';
import Share from '@/components/Share';
import HotCollect from '@/components/HotCollect';
import HotModel from '@/views/HotModel';
import DiscHighest from '@/views/DiscHighest';
import Login from '@/views/Login';
import Register from '@/views/Register';
import Axios from 'axios';
import Hotarticle from '@/views/Hotarticles';

// 蒋
import Detail from '@/components/Detail'
import Order from '@/components/Order'

//曾
import Sidebar from '@/components/Sidebar.vue'
import Car from '@/components/Car.vue'
import Screen from '@/views/Screen.vue'
import All from '@/views/All.vue'
import List from '@/views/List.vue'
import Config from '@/views/Config.vue'
import CarDetail from '@/views/CarDetail'

Vue.use(VueRouter)

const routes = [{
    path: '/',
    name: 'home',
    components: {
      search: Search,
      default: Home,
    },
    alias: '/home',
  },
  {
    path: '/reg',
    name: 'reg',
    components: {
      default: Register,
    }
  },
  {
    path: "/login",
    name: "login",
    components: {
      default: Login,
    },
    
  },
  {
    path: '/diary/:num',
    name: 'diary',
    components: {
      // search: DiaryTop,
      default: Diary,
    }
  },
  {
    path: '/share',
    name: 'share',
    components: {
      default: Share,
    }
  },
  {
    path: '/hotcollect/:num',
    name: 'hotcollect',
    components: {
      default: HotCollect,
    }
  },
  {
    path: '/hotarticle',
    name: 'hotarticle',
    components: {
      default: Hotarticle,
    }
  },

  // 蒋轶
  {
    path: '/detail/:num',
    name: 'detail',
    components: {
      default: Detail
    },

  },
  {
    path: '/order/:id/:i',
    components: {
      default: Order
    },
    beforeEnter: (to, from, next) => {
      let token = window.localStorage.getItem('token');
      if (token) {
        Axios.get('http://localhost:3000/users/login/verify?token=' + token).then((result) => {
          if (result.data.code == 1) {
            next();
          } else {
            next({ path: '/login' });
          }
        });
      } else {
        next({ path: '/login' });
      }
    }
  },

  // 曾帅
  {
    path: '/list',
    name: 'list',
    components: {
      search: Sidebar,
      default: Car,
    },
    children: [{
        path: '/',
        components: {
          // search: Search,
          default: Screen,
        }
      },
      {
        path: 'all',
        components: {
          // search: Search,
          default: All
        }
      },
      {
        path: 'list',
        components: {
          // search: Search,
          default:List,
        }
      },
    ]
  },

  {
    path: '/config',
    name: 'config',
    component: Config,

  },
  {
    path: '/detail/:bid/:sid',
    name: 'detail',
    components: {
      // search: Search,
      default: CarDetail,
    }

  }



  // {
  //   path: '/hotmodel',
  //   name: 'hotmodel',
  //   components: {
  //     default: HotModel,
  //   }
  // },
  // {
  //   path: '/dischigh',
  //   name: 'dischigh',
  //   components: {
  //     default: DiscHighest,
  //   }
  // }

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router