<template>
  <div>
    <div class="brand-header">
      <div class="wap_nav clearFix header_wap">
        <a href="javascript:history.back()" class="rtn demo-icon" id="seriesBackA"></a>
        <h1 class="wap_logo">
          <a href="/">
            <span class="img css_spd"></span>
          </a>
        </h1>
        <div class="search">
          <span class="clear" style="display: none;">×</span>
          <span class="demo-icon"></span>
          <form action="javascript:return true;">
            <input
              type="search"
              name
              placeholder="搜索车型"
              id="search"
              data-setting="{&quot;url&quot;:&quot;http://m.htche.com:80/search/view&quot;}"
              value
            />
          </form>
        </div>
        <div class="login">
          <a
            id="loginId"
            class="icon-wap_5"
            href="http://m.htche.com:80/login?upUrl=http://m.htche.com/brand/view"
          ></a>
        </div>
      </div>
    </div>

    <div class="brand-list-base brand-list-1">
      <div
        class="swiper-container swiper-container-vertical swiper-container-free-mode"
        style="display=block z-index=100000"
      >
        <div class="swiper-wrapper">
          <div class="swiper-slide swiper-slide-active">
            <a href="#">
              <img class="banner" :src="list[vuexi].b_bgimg" />
            </a>
            <dl v-for="(item,index) in list[vuexi].takeSeriesInfo" :key="index">
              <dt>{{item.firstLetter}}</dt>
              <dd v-for="(items,index) in item.seriesInfoList" :key="index">
                <a :href="'/detail/'+items.b_id+'/'+items.s_id" class="clearFix">
                  <div class="lf">
                    <img :src="items.image" />
                    <span class="price">￥{{items.maxPrice}}万起</span>
                  </div>
                  <div class="rt">
                    <h3>
                      <span class>{{items.name}}</span>
                      <span class="num">{{items.productCount}}</span>
                    </h3>
                    <p class="type">
                      <span>{{items.seatDes[0]}}</span>
                    </p>
                    <p class="des">{{items.desc}}</p>
                  </div>
                </a>
              </dd>
            </dl>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      list: [],
      // idx: this.$route.params.num
      idx:-1,
    };
  },
  created() {
    axios.get("http://m.htche.com/brand/infoList").then(res => {
      this.list = res.data.message;
      console.log(this.list);
      console.log(this.vuexi);
    });
  },
  methods: {
    vuex() {
      this.$store.commit("vuxiMut", this.idx);
    }
  },
  computed: {
    vuexi() {
      return this.$store.state.vuexi;
    }
  },
  updated() {
    this.vuex();
  }
};
</script>

<style scoped>
@import "../assets/css/brand.css";
@import "../assets/css/new-detail.css";
@import "../assets/css/common1.css";
@import "../assets/css/base1.css";
</style>