<template>
  <div id="hotarticle">
    <div class="doc fixed-head">
      <div class="wap_returnHead">
        <a href="javascript:history.back();" class="returnBtn">
          <span class="demo-icon"></span>
        </a>
        <div class="title">热门文章</div>
        <div class="ctrl">
          <a class="clear-btn" style="display: none;">清空</a>
        </div>
      </div>
      <div class="hot-article-list">
        <div class="item item2" v-for="(item,index) in articledata" :key="index">
          <a :href="'/diary/'+item.id" class="clearFix">
            <img
              :src="item.image"/>
            <h3>{{item.title}}</h3>
            <p class="p2">{{item.subtitle}}</p>
            <p class="p1 clearFix">
              <span class="s2">
                <i class="icon1"></i>{{item.parentTypeName}} · {{item.readNum}}人阅读
              </span>
            </p>
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
    name: 'hotarticle',
    data(){
        return{
            articledata:[],
        }
    },
    methods: {
        getarticledata(){
            this.$axios.get('http://m.htche.com/article/hot').then((result)=>{
                this.articledata = result.data.message;
                console.log(this.articledata);
            });
        }
    },
    created() {
        this.getarticledata();
    },
};
</script>
<style>
    @import '../assets/css/index.css';
</style>