import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    datalist: [],
    vuexi:0,   //曾帅
    
  },
  mutations: {
    addMut(state,obj){
      var list=state.datalist
      var bool=true
      list.forEach((item)=>{
        if(item.id==obj.id){
          bool=false
        }
      })
      if(bool){
        list.push(obj)
      }
    },
    delMut(state,i){
      var list=state.datalist
      list.splice(i,1)
    },
    delallMut(state){
      state.datalist=[]
    },

    // 曾帅
    vuxiMut(state,i){
      state.vuexi=i
   },
  },
  
})
